* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}.light{
  background-color: white;
  color: black;
}
.dark{
  background-color: black;
  color: white;
}
.container{
  width:100vw;
  height: 100vh;
  display:flex;
  background-color: lightgray;
  justify-content: center;
  align-items: center;
}
.calculator{
  width:50%;
  height:65%;
  border-radius: 20px;
  display:flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  box-shadow: 7px 3px 7px 3px rgb(99, 97, 97);
}
.inputbox{
  height:25%;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputbox input{
  height:80%;
  width:100%;
  padding-left: 18px;
  border-radius: 16px;
  display: flex;
  justify-content: center;
align-items: center;
border: none;
outline: none;
background-color: #f0f0f0;
font-size: 22px;
font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
box-shadow: 2px 2px 5px rgb(99, 97, 97);
}
.display{
  height:73%;
  width:90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  padding: 15px;
  gap:5px;
}
.row1, .row2,.row3, .row4, .row5{
  height:15%;
  width:100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.row1 button, .row2 button , .row3 button , .row4 button , .row5 button{
 height:100%;
 width:19%;
 display: flex;
 justify-content: center;
 font-family: 'Courier New', Courier, monospace;
 align-items: center;
 border-radius: 16px;
 border: none;
 outline: none;
 font-size: 14px;
 box-shadow: 2px 2px 5px rgb(99, 97, 97);
}
.row1 button:hover, .row2 button:hover , .row3 button:hover , .row4 button:hover , .row5 button:hover {
  transform:scale(1.04);
}
.highlight{
  background-color: rgb(0, 132, 255);
  color: white;
}
@media only screen and  (max-width:600px){
  .calculator{
      width:78%;
      height:62%;
      border-radius: 16px;
      box-shadow: 7px 3px 7px 3px rgb(99, 97, 97);
  }
  .inputbox input{
  font-size: 19px;
  }
  .display{
      padding-bottom:10px;
      gap:2px;
      width:100%;
  }
  .row1 button, .row2 button , .row3 button , .row4 button , .row5 button{
     border-radius: 12px;
     font-size: 13px;
     width:18.5%;
     height:98%;
  } 
}
@media only screen and  (max-width:900px) and (min-width:600px){
  .calculator{
      width:78%;
      height:62%;
      border-radius: 18px;
      box-shadow: 7px 3px 7px 3px rgb(99, 97, 97);
  }
  .inputbox input{
  font-size: 19px;
  }
  .display{
      padding-bottom:14px;
      gap:4px;
      width:90%;
  }
  .row1 button, .row2 button , .row3 button , .row4 button , .row5 button{
     border-radius: 16px;
     font-size: 16px;
     width:19%;
     height:100%;
  } 
}